import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'
import list from '@/pages/list'
import details from '@/pages/details'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/list',
      name: 'list',
      component: list
    },
    {
      path: '/details',
      name: 'details',
      component: details
    }
  ]
})



