//const baseURL = localStorage.getItem('baseURL') ? localStorage.getItem('baseURL') : 'http://192.168.52.100:6666';
const baseURL = localStorage.getItem('baseURL') ? localStorage.getItem('baseURL') : 'http://localhost:6666';
    console.log(localStorage.getItem('baseURL'));
// create an axios instance
const service = axios.create({
    baseURL: baseURL,
    timeout: 60000
})

service.interceptors.request.use(
    config => {
        return config
    },
    error => {
        return Promise.reject(error)
    }
)

service.interceptors.response.use(
    response => {
        if (response.status !== 200) {
            Message({
                message: res.msg || 'Error',
                type: 'error',
                duration: 5 * 1000
            });
            return Promise.reject(new Error(res.msg || 'Error'));
        } else {
            return response.data;
        }
    },
    error => {
        Message({
            message: error.message,
            type: 'error',
            duration: 5 * 1000
        })
        return Promise.reject(error)
    }
)

export default service


