<!-- 列表页 -->
<template>
  <div class='list'>
    <Table
      :tableData="tableData"
      :tableColumnData="tableColumnData"
      :height="'800px'"
      @columnClickCallBack="callBack"
    />
    <!-- <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[10, 20, 30, 40,50]"
      :page-size="pageSize"
      layout="prev, pager, next, sizes, jumper"
      :total="total"
      :background="true"
    >
    </el-pagination> -->
  </div>
</template>

<script>
import Table from "@/components/table.vue";
import { quotStockAll } from "@/api/list";

export default {
  components: {
    Table
  },
  props: [],
  data() {
    return {
      tableData: [],
      tableColumnData: [
        {
          prop: "code",
          label: "代码",
          isDifferentStates: "",
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: ""
        },
        {
          prop: "name",
          label: "名称",
          isDifferentStates: "",
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "preClosePrice",
          label: "前收盘价	",
          isDifferentStates: "",
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "openPrice",
          label: "开盘价",
          isDifferentStates: true,
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "tradePrice",
          label: "最新价",
          isDifferentStates: true,
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "highPrice",
          label: "最高价",
          isDifferentStates: true,
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "lowPrice",
          label: "最低价",
          isDifferentStates: true,
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "tradeAmt",
          label: "成交金额",
          isDifferentStates: "",
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "tradeVol",
          label: "成交数量",
          isDifferentStates: "",
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "tradeVolDay",
          label: "当日成交量",
          isDifferentStates: "",
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        },
        {
          prop: "tradeAmtDay",
          label: "当日成交金额",
          isDifferentStates: "",
          extraTextBefore: "",
          extraTextAfter: "",
          defaults: "0"
        }
      ],
      //国内指数定时器
      quotStockAllTimer: null,
      pageSize: 10,
      total: 400,
      page: 1
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.page = val;
      quotStockAll({
        page: this.page,
        pagesize: this.pageSize
      }).then(data => {
        this.tableData = data.items;
        this.pageSize = data.pagesize;
        this.total = data.pages;
      });
    },
    callBack(row) {
      console.log(row.code);
      this.$router.push({
        path: "/details",
        query: {
          id: row.code
        }
      });
    },
    //分钟换算
    formatSeconds(time) {
      return time * 60 * 1000;
    },
    quotStockAll() {
      let that = this;
      // quotStockAll({
      //   page: that.page,
      //   pagesize: that.pageSize
      // }).then(data => {
      //   console.log("数据返回成功");
      //   console.log(data);
      //   that.tableData = data.items;
      //   that.total = data.pages;
      //   that.quotStockAllTimer = setInterval(() => {
      //     console.log("正在请求");
      //     that.quotStockAll();
      //     clearInterval(that.quotStockAllTimer);
      //     console.log("请求结束");
      //   }, that.formatSeconds(1));
      // });
      quotStockAll().then(data => {
        console.log("数据返回成功");
        console.log(data);
        that.tableData = data.items;
        that.total = data.pages;
        that.quotStockAllTimer = setInterval(() => {
          console.log("正在请求");
          that.quotStockAll();
          clearInterval(that.quotStockAllTimer);
          console.log("请求结束");
        }, that.formatSeconds(1));
      });
    }
  },
  created() {},
  mounted() {
    this.quotStockAll();
  },
  destroyed() {
    clearInterval(this.quotStockAllTimer);
  }
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
.list {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
</style>


